.template-item {
  margin-bottom: 50px;
  border-radius: 2px;
  position: relative;
}

.template-item:hover img {
  opacity: 0.7;
}

.template-item .ticker {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  color: #fff;
  z-index: 1;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 700;
  background: #4be6e6;
}

.template-item a {
  box-shadow: 0 15px 45px -9px rgba(0, 0, 0, 0.25);
  display: block;
}

.template-item a img {
  border-radius: 5px;
  transition: .3s all;
}

.template-item h4 {
  margin-top: 20px;
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: 600;
  color: #252525;
  text-transform: uppercase;
}

.template-item a {
  display: inline-block;
  position: relative;
}

.call-to-action {
  background: #3483de;
  color: #fff;
  padding: 80px 0;
}

.call-to-action h2 {
  line-height: 45px;
}

.call-to-action .btn-buy-button {
  background: #fff;
  padding: 18px 45px;
  border-radius: 30px;
  margin-top: 20px;
  font-weight: 600;
  font-size: 12px;
  display: inline-block;
  text-transform: uppercase;
  color: #666;
}

#mouse-scroll {
  style: block;
}

#mouse-scroll {
  position: absolute;
  margin: auto;
  left: 50%;
  -webkit-transform: translateX(-50%);
  z-index: 9999;
  bottom: 50px;
}

#mouse-scroll span {
  display: block;
  width: 10px;
  height: 10px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  transform: rotate(45deg);
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  margin: 0 0 3px 5px;
}

#mouse-scroll .mouse {
  height: 40px;
  width: 22px;
  border-radius: 10px;
  -webkit-transform: none;
  transform: none;
  border: 2px solid #ffffff;
  top: 170px;
}

#mouse-scroll .mouse-in {
  height: 5px;
  width: 2px;
  display: block;
  margin: 5px auto;
  background: #ffffff;
  position: relative;
}

#mouse-scroll .mouse-in {
  -webkit-animation: animated-mouse 1.2s ease infinite;
  moz-animation: mouse-animated 1.2s ease infinite;
}

@-webkit-keyframes animated-mouse {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
  }
}

@-webkit-keyframes mouse-scroll {
  0% {
    opacity: 1;
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

